___CSS_LOADER_EXPORT___.push([module.id, `body {
    display: grid;
    place-items: center;
    font-family: 'Segoe UI', sans-serif;
    background-color: #313131;
  
    margin: 0;
  }
  
  .conteudo {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 0 5%;
  }
  
  h1 {
    font-size: 3rem;
    font-weight: 700;
    color: #f9f9f9;
  }
  
  .descricao {
    font-size: 22px;
    font-weight: 600;
    color: #f9f9f9;
  }
  
  .container-formulario {
    display: flex;
    /* justify-content: center; */
    /* min-width: 450px; */
  
    gap: 5px;
  }
  
  .pesquisa {
    outline: 1px solid #f9f9f9 important;
  
    border:none;
    border-radius: 10px;
  
    font-size: 18px;
  
    padding: 5px;
  }
  
  .btn {
    display: grid;
    padding: 15px;
  
    border: none;
    border-radius: 10px;
  }
  
  .btn:hover {
    opacity: 0.9;
    cursor: pointer;
    outline: 1px solid #ffffff;
  }
  
  .btn-pesquisar {
    background-color: #4DAD5B;
    color: white;
  }
  
  .btn-limpar {
    background-color: #DA471B;
    color: white;
  }
  
  .card-pokemon {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
  
    max-width: 350px;
    height: 450px;
  
    background-color: #e1e1e1;
    color: #313131;
  
    border-radius: 1rem;
  
    margin-top: 5%;
  }
  
  .notificacao {
    position: absolute;
    bottom: 0;
  
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    
    background-color: #DA471B;
    border-radius: 10px;
  
    min-width: 300px;
  
    padding: 10px 20px;
    margin-bottom: 1%;
    margin-left: auto;
    margin-left: right;
  
    transition: outline 1s ease-in-out;
  }

  .pnlPokemon {
    background-color: #bebebe;
    display: flex;
    gap: 2px;
    border-radius: 5px;
    flex-direction: column;
    align-items: center;
    width: 150px;
  }
  
  .notificacao:hover {
    opacity: 0.9;
    cursor: pointer;
  }`, "",{"version":3,"sources":["webpack://./src/views/pokemon-detalhes/pokemon-detalhes.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,mBAAmB;IACnB,mCAAmC;IACnC,yBAAyB;;IAEzB,SAAS;EACX;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,aAAa;EACf;;EAEA;IACE,eAAe;IACf,gBAAgB;IAChB,cAAc;EAChB;;EAEA;IACE,eAAe;IACf,gBAAgB;IAChB,cAAc;EAChB;;EAEA;IACE,aAAa;IACb,6BAA6B;IAC7B,sBAAsB;;IAEtB,QAAQ;EACV;;EAEA;IACE,oCAAoC;;IAEpC,WAAW;IACX,mBAAmB;;IAEnB,eAAe;;IAEf,YAAY;EACd;;EAEA;IACE,aAAa;IACb,aAAa;;IAEb,YAAY;IACZ,mBAAmB;EACrB;;EAEA;IACE,YAAY;IACZ,eAAe;IACf,0BAA0B;EAC5B;;EAEA;IACE,yBAAyB;IACzB,YAAY;EACd;;EAEA;IACE,yBAAyB;IACzB,YAAY;EACd;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;;IAET,gBAAgB;IAChB,aAAa;;IAEb,yBAAyB;IACzB,cAAc;;IAEd,mBAAmB;;IAEnB,cAAc;EAChB;;EAEA;IACE,kBAAkB;IAClB,SAAS;;IAET,kBAAkB;IAClB,eAAe;IACf,gBAAgB;;IAEhB,yBAAyB;IACzB,mBAAmB;;IAEnB,gBAAgB;;IAEhB,kBAAkB;IAClB,iBAAiB;IACjB,iBAAiB;IACjB,kBAAkB;;IAElB,kCAAkC;EACpC;;EAEA;IACE,yBAAyB;IACzB,aAAa;IACb,QAAQ;IACR,kBAAkB;IAClB,sBAAsB;IACtB,mBAAmB;IACnB,YAAY;EACd;;EAEA;IACE,YAAY;IACZ,eAAe;EACjB","sourcesContent":["body {\r\n    display: grid;\r\n    place-items: center;\r\n    font-family: 'Segoe UI', sans-serif;\r\n    background-color: #313131;\r\n  \r\n    margin: 0;\r\n  }\r\n  \r\n  .conteudo {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 10px;\r\n    padding: 0 5%;\r\n  }\r\n  \r\n  h1 {\r\n    font-size: 3rem;\r\n    font-weight: 700;\r\n    color: #f9f9f9;\r\n  }\r\n  \r\n  .descricao {\r\n    font-size: 22px;\r\n    font-weight: 600;\r\n    color: #f9f9f9;\r\n  }\r\n  \r\n  .container-formulario {\r\n    display: flex;\r\n    /* justify-content: center; */\r\n    /* min-width: 450px; */\r\n  \r\n    gap: 5px;\r\n  }\r\n  \r\n  .pesquisa {\r\n    outline: 1px solid #f9f9f9 important;\r\n  \r\n    border:none;\r\n    border-radius: 10px;\r\n  \r\n    font-size: 18px;\r\n  \r\n    padding: 5px;\r\n  }\r\n  \r\n  .btn {\r\n    display: grid;\r\n    padding: 15px;\r\n  \r\n    